                            .auth__fieldset-item
                                Input(label="" v-model="password" type="password")
                        .auth__submit
                            button(class="button" @click="submit" v-if="!loading") 
                            button(class="button" v-else) ...
</template>

<script>
import {
    Icon,
    Input
} from '@/components'
import Vue from "vue";

export default {
    name: "auth",
    components: {
        Icon, Input
    },
    data() {
        return {
            inn: '',
            login: '',
            password: '',
            errors: [],
            loading: false,
        }
    },
    computed: {
        isLoggedIn () {
            return this.$store.getters.cuser.isLoggedIn
        }
    },
    mounted() {
        if (this.isLoggedIn) {
            console.log(' ,  ...')
        }
    },
    methods: {
        checkin () {
            this.errors = []
            if (this.inn.length <= 10) {
                this.errors.push('  ')
            }
            if (this.login.length < 3) {
                this.errors.push('  ')
            }
            if (this.password.length < 3) {
                this.errors.push('  ')
            }
        },
        async submit () {
            this.loading = true
            this.checkin()

            if (this.errors.length === 0) {
                const userObject = {
                    inn: this.inn,
                    login: this.login,
                    password: this.password,
                }
                await this.$store.dispatch('authUser', userObject)
                    .then(res => {
                        this.errors.push(res)
                    })

            }
            this.loading = false
        }
    }
}
</script>

